/* Top section of the navbar */
.navbar-top {
    background-color: #5b0606;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    font-size: 12px;
  }
  
  .navbar-right-links a {
    margin-left: 15px;
    text-decoration: none;
    color: #141010;
  }
  
  /* Main navbar */
  .navbar-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1px 10px;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .logo img {
    max-width: 140px;
  }

  .threedot img {
    max-width: 50px;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    gap: 20px;
  }
  
  .nav-links li {
    font-size: 16px;
  }
  
  .nav-links a {
    text-decoration: none;
    color: #000;
  }
  
  .search-bar {
    display: flex;
    align-items: center;
  }
  
  .search-bar input {
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 5px 0 0 5px;
    outline: none;
    width: 250px;
  }
  
  .search-bar button {
    padding: 8px 15px;
    border: none;
    background-color: #ff5722;
    color: #fff;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
  }
  
  .cart-icon img {
    width: 35px;
    cursor: pointer;
  }
  